import { useEffect, useState } from "react";
import { editAddressValidation } from './validation'
import Axios from "../../api/Axios";
import { useNavigate, useParams, useSearchParams } from 'react-router-dom';
import { State, City } from "country-state-city";
import Select from "react-select";
import store from "../../store";

const axios = new Axios();

const EditAddress = () => {

  const { addressType } = useParams()
  const [searchParams, setSearchParams] = useSearchParams();
  let currentUser = store.getState().user.currentUser
  const id = searchParams.get('id')
  const firstAddress = searchParams.get("firstAddress")
  let firstName = ""
  let lastName = ""
  let email = ""
  let phone = ""
  if(firstAddress && firstAddress == "true"){
     firstName = currentUser.firstName
     lastName = currentUser.lastName
     email = currentUser.email
     phone = currentUser.contact
  }
  const navigate = useNavigate()
  if (!["billing-address", "shipping-address"].includes(addressType)) {
    navigate("/404")
  }
  const [error, setError] = useState({
    firstName: false,
    lastName: false,
    email: false,
    state: false,
    streetAddress: false,
    city: false,
    postcode: false,
    phone: false,
    message: ""
  });

  const [data, setData] = useState({
    firstName: firstName,
    lastName: lastName,
    email: email,
    companyName: "",
    gstNumber: "",
    country: "India",
    state: "",
    streetAddress: "",
    landmark: "",
    city: "",
    postcode: "",
    phone: phone
  });

  const [selectedCountryId, setSelectedCountryId] = useState("IN")
  const [selectedStateId, setSelectedStateId] = useState("")
  const [cityValue, setCityValue] = useState({ label: null })

  const updatedStates = (countryId) => State.getStatesOfCountry(countryId).map((state) => ({ label: state.name, value: state.name, id: state.isoCode }));

  const updatedCities = (countryid, stateId) => City.getCitiesOfState(countryid, stateId).map((city) => ({ label: city.name, value: city.name }))


  const onChange = (value, target) => {
    setData({
      ...data,
      [target]: value,
    });
    if (error.email === true || error.city === true || error.firstName === true || error.lastName === true || error.country === true || error.phone || error.postcode || error.state || error.streetAddress) {
      setError({
        ...error,
        [target]: false
      })
    }
  };

  const getUserAccountDetails = async () => {
    const options = {
      method: "GET"
    }
    const result = await axios.callApi(`user/getAddressById/${id}`, options)
    if (result.status == 200) {
      setData({
        ...data,
        id: result.data.data.id,
        firstName: result.data.data.firstName,
        lastName: result.data.data.lastName,
        email: result.data.data.email,
        companyName: result.data.data.companyName,
        gstNumber: result.data.data.gstNumber,
        country: result.data.data.country,
        state: result.data.data.state,
        streetAddress: result.data.data.streetAddress,
        landmark: result.data.data.landmark,
        city: result.data.data.city,
        postcode: result.data.data.pincode + "",
        phone: result.data.data.contact
      })
      setCityValue({ label: result.data.data.city })
      setSelectedStateId(result.data.data.stateId)
    }
  }

  const handleSubmit = async (e) => {
    try {
      e.preventDefault()
      const valid = editAddressValidation(data)
      cityValue.label == null ? setError({ ...valid, city: true }) : setError(valid)
      if (valid.city || valid.firstName || valid.lastName || valid.email || valid.country || valid.phone || valid.postcode || valid.state || valid.streetAddress || cityValue.label == null) {
        return
      }
      const options = {
        method: "POST",
        body: { ...data, selectedStateId, addressType: (addressType == "billing-address") ? "billing" : "shipping" }
      }
      const result = await axios.callApi(`user/createEditAddresses`, options)
      if (result.status == 200) {
        navigate("/my-account/addresses")
      }
    }
    catch (error) {
      console.error("Something went wrong")
    }
  }

  useEffect(() => {
    if (id)
      getUserAccountDetails()
  }, [])

  return (
    <div className="edit-account-container container-fluid" style={{ backgroundColor: "white" }}>
      <div className="text-style">
        {addressType === "billing-address" ? "Billing address" : "Shipping address"}
      </div>
      <form>
        <div className="row" style={{ marginTop: "25px" }}>
          <div className='col-sm-12 col-lg-6 form-group mb-2'>
            <label htmlFor="firstName" className='form-label'>First Name</label>
            <input type="text" id='firstName' className={`form-control ${error.firstName ? "errorInputField" : ""}`} required onChange={e => onChange(e.target.value, 'firstName')} value={data?.firstName} />
            {error.firstName && <div className='error'>
              Please enter your first name
            </div>}
          </div>
          <div className='col form-group mb-2'>
            <label htmlFor="lastName" className='form-label'>Last Name</label>
            <input type="text" id='lastName' className={`form-control ${error.lastName ? "errorInputField" : ""}`} required onChange={e => onChange(e.target.value, 'lastName')} value={data?.lastName} />
            {error.lastName && <div className='error'>
              Please enter your last name
            </div>}
          </div>
        </div>
        <div className='form-group mb-2'>
          <label htmlFor="companyName" className='form-label'>Company name (Optional)</label>
          <input type="text" id='companyName' className="form-control" onChange={e => onChange(e.target.value, 'companyName')} value={data?.companyName} />
        </div>
        <div className='form-group mb-2'>
          <label htmlFor="gstNumber" className='form-label'>GST No. (Optional)</label>
          <input type="text" id='gstNumber' className="form-control" onChange={e => onChange(e.target.value, 'gstNumber')} value={data?.gstNumber} />
        </div>
        <div className="row">
          <div className='col-sm-12 col-lg-6 form-group mb-2'>
            <label htmlFor="streetAddress" className='form-label'>Address</label>
            <input type="text" id='streetAddress' className={`form-control ${error.streetAddress ? "errorInputField" : ""}`} required onChange={e => onChange(e.target.value, 'streetAddress')} value={data?.streetAddress} />
            {error.streetAddress && <div className='error'>
              Please enter your address
            </div>}
          </div>
          <div className='col form-group mb-2'>
            <label htmlFor="landmark" className='form-label'>Landmark (Optional)</label>
            <input type="text" id="landmark" className="form-control" onChange={e => onChange(e.target.value, 'landmark')} value={data?.landmark} />
          </div>
        </div>
        <div className="row">
          <div className='col-sm-12 col-lg-6 form-group mb-2'>
            <label className='form-label'>Country</label>
            <Select
              className={`${error.country ? "errorSelectField" : ""}`}
              options={[{
                "label": "India",
                "value": "India",
                "id": "IN"
              }]}
              onChange={e => {
                onChange(e.value, 'country')
              }}
              value={{ label: 'India' }}
            />
          </div>
          <div className='col form-group mb-2'>
            <label htmlFor="state" className='form-label'>State</label>
            <Select
              className={`${error.state ? "errorSelectField" : ""}`}
              options={updatedStates(selectedCountryId)}
              onChange={e => {
                setSelectedStateId(e.id)
                setCityValue({ label: null })
                onChange(e.value, 'state')
              }}
              value={data.state && { label: data.state }}
            />
            {error.state && <div className='error'>
              Please enter your state
            </div>}
          </div>
        </div>
        <div className='form-group mb-2'>
          <label htmlFor="city" className='form-label'>City</label>
          <Select
            className={`${error.city ? "errorSelectField" : ""}`}
            options={updatedCities(selectedCountryId, selectedStateId ? selectedStateId : null)}
            onChange={e => {
              setCityValue({ label: e.value })
              onChange(e.value, 'city')
            }}
            value={cityValue.label && cityValue}
          />
          {error.city && <div className='error'>
            Please enter your city
          </div>}
        </div>
        <div className='form-group mb-2'>
          <label htmlFor="postcode" className='form-label'>Pincode</label>
          <input type="number" id='postcode' className={`form-control ${error.postcode ? "errorInputField" : ""}`} required onChange={e => onChange(e.target.value, 'postcode')} value={data?.postcode} />
          {error.postcode && <div className='error'>
            Please enter your pincode
          </div>}
        </div>
        <div className='form-group mb-2'>
          <label htmlFor="phone" className='form-label'>Phone</label>
          <input type="number" id='phone' className={`form-control ${error.phone ? "errorInputField" : ""}`} required onChange={e => onChange(e.target.value, 'phone')} value={data?.phone} />
          {error.phone && <div className='error'>
            Please enter valid phone number
          </div>}
        </div>
        <div className='form-group mb-2'>
          <label htmlFor="email" className='form-label'>Email Address</label>
          <input type="email" id='email' className={`form-control ${error.email ? "errorInputField" : ""}`} required onChange={e => onChange(e.target.value, 'email')} value={data?.email} />
          {error.email && <div className='error'>
            {error.message ? "Please enter a valid email" : "Please enter your email"}
          </div>}
        </div>
        <button className='btn btn-dark mt-5 mb-2' onClick={e => handleSubmit(e)}>SAVE ADDRESS</button>
      </form>
    </div>
  )
}

export default EditAddress
